import {useNavigate} from "react-router";
import {li} from "../styles/style";
import {useEffect, useState} from "react";
import isAuth from "../services/isAuth";


const HeaderComponent = (props) => {
    const navigate = useNavigate();
    const [auth, setAuth] = useState("");
    const Admin = "ROLE_ADMIN";

    const logout = () => {
        if (window.confirm("  ?")) {
            window.sessionStorage.removeItem("accessToken");
            navigate("/go/login");
        } else {
            return false;
        }
    }

    const goMyPage = () => {
        if (auth == null) {
            alert(" ");
            return false;
        }
        navigate("/go/common/myPage")
    }



    useEffect(() => {
        setAuth(isAuth());
        // isLogin();
        console.log("");
    },)


    return (
        <div className="sider siderOn">
            <ul style={{border: "1px solid black", margin: "0px"}}>
                <li style={li}
                    onClick={() => {
                        navigate("/")
                    }
                    }
                    // className="Fli on cmli"
                >
                    
                </li>
                <li
                    style={li}
                    onClick={() => {
                        navigate("/go/main")
                    }
                    }
                    // className="Fli on cmli"
                >
                    
                </li>


                <li
                    style={li}
                    onClick={() => navigate("/go/test/jpa")}
                >
                      
                </li>
                <li
                    style={li}
                    onClick={() => navigate("/go/login")}
                >
                    
                </li>
                <li
                    style={li}
                    onClick={() => logout()}
                >
                    
                </li>

                {
                    auth === Admin
                        ?
                        <li
                            style={li}

                        >
                             
                        </li>
                        :
                        null
                }

                <li
                    style={li}
                    onClick={() => {
                        goMyPage();
                    }
                    }
                >
                     
                </li>


                <li
                    style={li}
                    onClick={() => navigate("/go/calculator")}
                >
                    
                </li>

                <p>
                      ROLE :
                    {
                        auth != null
                            ?
                            auth
                            :
                            ""
                    }
                </p>


            </ul>
        </div>

    )
}

export default HeaderComponent